
html, body, div {
  font-family: Verdana, Geneva, sans-serif;
}
#app {
  margin: 20px auto;
  max-width: 800px;
}

h2 {
  border-bottom: solid 1px #000;
}
h3 {
  margin-top: 0;
}
samp {
  font-family: Consolas, Monaco, monospace;
  font-weight: bold;
}
tt {
  background-color: #d0dfef;
  font-family: Consolas, Monaco, monospace;
  padding: 1px 5px;
}
#config-form {
  margin: 20px auto;
  max-width: 800px;
}
.config-item {
  background-color: #e0efff;
  margin: 4px 0;
  padding: 10px;
}
input[type=text] {
  width: 300px;
}

.env-line {
  font-family: Consolas, Monaco, monospace;
}
















































